body {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: "Patrick Hand", cursive;
  background-image: -webkit-gradient(linear, left top, right top, from(#94ddbc), to(#00bfb3));
  background-image: -webkit-linear-gradient(left, #94ddbc, #00bfb3);
  background-image: -o-linear-gradient(left, #94ddbc, #00bfb3);
  background-image: linear-gradient(to right, #94ddbc, #00bfb3);
}



